body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3d3d3d !important;
}

.header {
  background-color: #f2f4f6;
  text-align: center;
  padding: 20px;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
}


body {
  min-height: 100vh;
  position: relative;
  margin: 0;
  padding-bottom: 100px;
  box-sizing: border-box;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: #f2f4f6;
  padding: 20px;
  margin-top: 20px;
  -webkit-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
}

.border-gradient {
  border: 5px solid !important;
  border-image-slice: 1 !important;
  border-width: 5px !important;
}

.border-gradient-bottom {
  border-bottom: 10px solid !important;
  border-image-slice: 1 !important;
  border-width: 5px !important;
}

.border-gradient-shippypro {
  border-image-source: linear-gradient(to left, #ff01a2, #05ffde);
}

.logo {
  height: 8vmin;
  pointer-events: none;
}

.btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.disabled {
  pointer-events: none;
}

.card {
  padding: 0 !important;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  border: 5px solid rgba(0,0,0,1) !important;
}

.card .card-title {
  font-size: 1.5rem;
}

.card .card-footer {
  font-size: 1.2rem;
  font-weight: bold;
}

.searchForm {
  background-color: #fff;
  padding: 20px;
  justify-content: space-between;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
}

.preloader {
  display: flex;
  justify-content: center;
}

@keyframes blink {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.preloader .img {
  height: 10vmin;
  pointer-events: none;
  animation: blink 1s;
  animation-iteration-count: infinite;
}

.select {
  margin-bottom: 10px;
}

.validationSearch {
  color: red;
  font-size: 20px;
}

.flightMessage {
  background-color: #fff;
  padding: 20px;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
}

@media (min-width: 768px) {
  .select {
    margin-bottom: 0;
  }
}

